import moment from "moment";

export default function InvoicePDF(props) {
  const { invoiceData } = props;

  return (
    <>
      <div className="w-100 d-flex justify-content-between align-items-center">
        <p className="mb-0 Invoice-Number fw-" style={{ color: "#999999" }}>
          Invoice {invoiceData?.invoiceNumber}
        </p>
      </div>
      <div className="w-100 d-flex justify-content-between align-items-center my-3">
        <div className="d-flex align-items-center">
          <div>
            <img
              src={
                invoiceData?.doctorProfileImage
                  ? invoiceData?.doctorProfileImage
                  : `https://ui-avatars.com/api/?name=${`doctorName`}&background=6045eb&color=fff`
              }
              className="object-fit-cover rounded-5"
              style={{ width: "4rem", height: "4rem" }}
              alt="Payment Invoice"
            />
          </div>
          <div className="px-3">
            <span className="fw-bold">Dr. {invoiceData?.doctorName}</span>
            <div style={{ color: "#999999", fontSize: "14px" }}>
              {moment(invoiceData?.createdDate).format("DD MMM YYYY")}
            </div>
          </div>
        </div>
        <h6>{invoiceData?.status}</h6>
      </div>
      <hr style={{ color: "#c8cfd5" }} />
      <div className="mt-3 d-flex justify-content-between">
        <div>
          <label style={{ color: "#B3B3B3" }}>From:</label>
          <h5 className="fs-6 fw-bold">{invoiceData?.patientName}</h5>
          <p className="Address-FontSize fw-bold mb-2">
            {invoiceData?.location}
          </p>
          <small>{invoiceData?.email}</small>
          <p className="Address-FontSize mt-2">{invoiceData?.phoneNumber}</p>
        </div>

        <div>
          <label style={{ color: "#B3B3B3" }}>Bill to:</label>
          <h5 className="fs-6 fw-bold">Family Doc 24/7.</h5>
          <p className="Address-FontSize fw-bold mb-2">
            Fitzwilliam Hall, Fitzwilliam Place, Dublin 2
          </p>
          <small>sales@familydoc247.ie</small>
          <p className="Address-FontSize mt-2">+353 12630959</p>
        </div>
      </div>
      <hr style={{ color: "#c8cfd5" }} />
      <div>
        <h5 className="fs-6 fw-bold mt-4">Description</h5>
        <div className="d-flex justify-content-between">
          <h6 className="mb-2" style={{ color: "#B3B3B3" }}>
            Item
          </h6>
          <h6 className="pe-5 mb-2" style={{ color: "#B3B3B3" }}>
            Fee
          </h6>
        </div>
      </div>
      <div className="fs-6 fw-bold d-flex justify-content-between">
        <h6>
          {invoiceData?.appointmentReason
            ? invoiceData?.appointmentReason
            : "N/A"}
        </h6>
        <h6 className="me-5">
          {invoiceData?.amount ? `$${invoiceData?.amount}` : "N/A"}
        </h6>
      </div>
    </>
  );
}
